.main-title{
    text-align: center;
    margin-top: 3em;
  }
  .timer{
    display: grid;
    justify-content: center;
    text-align: center;
  }
  #timer-label{
    text-align: center;
  }
  #inputs{
    display: flex;
  }
  .btn-wrapper{
    display: flex;
  }
  #break, #session{
    margin: 1em;
  }
  #start_stop, #reset{
    border-radius: 20%;
    width: 6em;
  }
  .session-break-btn{
    width: 20%;
    margin: auto;
    border-radius: 40%;
  }
